// 导入 eventBus 模块
import bus from '@/components/home/eventBus.js'; // 导入 store 才可以修改里面的属性，若在计算属性中用 mapState 解构的是只读不可修改的 

import store from '@/store/index.js';
import { getSearchSingle } from '@/request/api/search.js';
import { mapMutations } from 'vuex';
export default {
  data() {
    return {
      // 是否显示加载中
      isLoading: 1,
      // 默认每次请求数量
      limit: 100,
      // 决定请求返回的数据范围
      offset: 0,
      // 当前页
      currentPage: 1,
      // 接收的歌曲
      songs: [],
      // 零
      zero: 0,
      // 搜索的关键词
      searchKey: '',
      // 查到的单曲数量
      songCount: 0,
      // html渲染的歌曲名
      vHtmlSongName: '',
      // 是否显示v-html嵌入的歌曲名关键字html标签
      isVHtmlSongName: 0,
      // html渲染的歌手
      vHtmlSinger: '',
      // 是否显示v-html嵌入的歌手关键字html标签
      isVHtmlSinger: 0
    };
  },

  // 写在 created 阶段（现成的解决方案）
  created() {
    // 通过 eventBus 接收从 TopNav 传过来的 searchKey
    bus.on('searchKey', val => {
      // console.log('SearchSingle 接收到到 searchKey', val);
      this.searchKey = val;
    });
  },

  mounted() {
    // 初始需要从 vuex 获取关键词让其高亮
    this.searchKey = store.state.searchKey; // 搜索单曲

    if (sessionStorage.getItem('searchKey')) this.getSingle();
  },

  methods: {
    // 搜索单曲
    async getSingle() {
      // let res = await getSearchSingle(store.state.searchKey, this.limit, this.offset)
      let res = await getSearchSingle(sessionStorage.getItem('searchKey'), this.limit, this.offset); // console.log('搜索单曲', res);

      this.songCount = res.data.result.songCount;
      this.songs = res.data.result.songs;
      if (this.songs.length !== 0) this.isLoading = 0; // console.log('单曲', this.songs);
    },

    // 换页
    pageChange() {
      this.offset = (this.currentPage - 1) * this.limit; // console.log('当前页码：', this.currentPage, '数据偏移量：', this.offset);
      // 重新搜索单曲

      this.getSingle();
    },

    // 格式化播放时长
    formatDuration(time) {
      return this.$formatDuration(time);
    },

    // 格式化单曲序号
    formatIndex(index) {
      return this.$formatIndex(index);
    },

    // 歌曲名关键词高亮显示
    activeSongName(string) {
      let reg = new RegExp(this.searchKey, 'g');
      let replaceString = '<span style="color: rgb(27, 94, 195)">' + this.searchKey + '</span>';
      string = string.replace(reg, replaceString);

      if (string.indexOf(this.searchKey) == -1) {
        this.isVHtmlSongName = 0;
        return string;
      } else {
        this.isVHtmlSongName = 1;
        this.vHtmlSongName = string;
        return '';
      }
    },

    // 歌手关键词高亮显示
    activeSinger(string) {
      let reg = new RegExp(this.searchKey, 'g');
      let replaceString = '<span style="color: rgb(27, 94, 195)">' + this.searchKey + '</span>';
      string = string.replace(reg, replaceString);

      if (string.indexOf(this.searchKey) == -1) {
        this.isVHtmlSinger = 0;
        return string;
      } else {
        this.isVHtmlSinger = 1;
        this.vHtmlSinger = string;
        return '';
      }
    },

    // 对单曲数量的处理
    songCountChange(num) {
      if (num > 300) return 300;
      return num;
    },

    // 去往专辑详情页
    goSongListDetail(id) {
      this.$router.push({
        path: '/SongListDetail',
        query: {
          id: id,
          // 1歌单 2专辑
          type: 2
        }
      });
    },

    // 去往歌手页
    goSingerPage(id) {
      this.$router.push({
        path: '/SingerPage',
        query: {
          id: id
        }
      });
    },

    // 双击播放歌单列表
    playSongList(index) {
      // 显示底部音乐播放组件
      this.updateIsPlayBox(1); // 更新播放列表

      this.updatePlayList(this.songs); // 更新歌单名(歌曲来源)

      this.updateSongListName('搜索页'); // 更新歌单 id(歌曲来源)

      this.updateSongListId(2); // 更新播放列表下标

      this.updatePlayListIndex(index);
    },

    ...mapMutations(['updateIsPlayBox', 'updatePlayList', 'updateSongListName', 'updateSongListId', 'updatePlayListIndex'])
  },
  watch: {
    // 监听从 TopNav 传过来的 searchKey
    searchKey() {
      this.isLoading = 1;
      this.getSingle();
    }

  }
};