// 导入 axios 实例
import service from "..";

// 搜索单曲
export function getSearchSingle(data, limit, offset) {
  return service({
    method: 'GET',
    // ！默认开始搜索 } 但解决了很多 bug，不多写 { 请求不了数据
    url: `/search?keywords=${data}}&type=1&limit=${limit}&offset=${offset}`
  })
}

// 搜索歌手 data 歌手名 传入 num 页码返回相应数量的相关歌手
export function getSearchSinger(data, num) {
  return service({
    method: 'GET',
    url: `/search?keywords=${data}&type=100&limit=${num*20}`
  })
}

